    color: #c0c4cc;
}

.el-input__inner::placeholder {
    color: #c0c4cc;
}

.el-input__inner:hover {
    border-color: #c0c4cc;
}

.el-input__inner:focus {
    outline: none;
    border-color: #1890ff;
}

.el-input__suffix {
    position: absolute;
    height: 100%;
    right: 5px;
    top: 0;
    text-align: center;
    color: #c0c4cc;
    -webkit-transition: all .3s;
    transition: all .3s;
    pointer-events: none;
}

.el-input__suffix-inner {
    pointer-events: all;
}

.el-input__prefix {
    position: absolute;
    height: 100%;
    left: 5px;
    top: 0;
    text-align: center;
    color: #c0c4cc;
    -webkit-transition: all .3s;
    transition: all .3s;
}

.el-input__icon {
    height: 100%;
    width: 25px;
    text-align: center;
    -webkit-transition: all .3s;
    transition: all .3s;
    line-height: 32px;
}

.el-input__icon:after {
    content: '';
    height: 100%;
    width: 0;
    display: inline-block;
    vertical-align: middle;
}

.el-input__validateIcon {
    pointer-events: none;
}

.el-input.is-active .el-input__inner {
    outline: none;
    border-color: #1890ff;
}

.el-input.is-disabled .el-input__inner {
