.el-button {
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #fff;
    border: 1px solid #dcdfe6;
    border-color: #dcdfe6;
    color: #606266;
    -webkit-appearance: none;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: none;
    margin: 0;
    -webkit-transition: .1s;
    transition: .1s;
    font-weight: 500;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    padding: 8px 15px;
    font-size: 14px;
    border-radius: 4px;
}

.el-button+.el-button {
    margin-left: 10px;
}

.el-button.is-round {
    padding: 8px 15px;
}

.el-button:hover,
.el-button:focus {
    color: #1890ff;
    border-color: #1890ff;
    background-color: #e8f4ff;
}

.el-button:active {
    color: #1682e6;
    border-color: #1682e6;
    outline: none;
}

.el-button::-moz-focus-inner {
    border: 0;
}

.el-button [class*="el-icon-"]+span {
    margin-left: 5px;
}

.el-button.is-plain:hover,
.el-button.is-plain:focus {
    background: #fff;
    border-color: #1890ff;
    color: #1890ff;
}

.el-button.is-plain:active {
    background: #fff;
    border-color: #1682e6;
    color: #1682e6;
    outline: none;
}

.el-button.is-active {
    color: #1682e6;
    border-color: #1682e6;
}

.el-button.is-disabled,
.el-button.is-disabled:hover,
.el-button.is-disabled:focus {
    color: #c0c4cc;
    cursor: not-allowed;
    background-image: none;
    background-color: #fff;
    border-color: #ebeef5;
}

.el-button.is-disabled.el-button--text {
    background-color: transparent;
}

.el-button.is-disabled.is-plain,
.el-button.is-disabled.is-plain:hover,
.el-button.is-disabled.is-plain:focus {
    background-color: #fff;
    border-color: #ebeef5;
    color: #c0c4cc;
}

.el-button.is-loading {
    position: relative;
    pointer-events: none;
}

.el-button.is-loading:before {
    pointer-events: none;
    content: '';
    position: absolute;
    left: -1px;
    top: -1px;
    right: -1px;
    bottom: -1px;
    border-radius: inherit;
    background-color: rgba(255, 255, 255, 0.35);
}

.el-button.is-round {
    border-radius: 20px;
    padding: 12px 23px;
}

.el-button.is-circle {
    border-radius: 50%;
    padding: 9px;
}

.el-button--primary {
    color: #fff;
    background-color: #1890ff;
    border-color: #1890ff;
}

.el-button--primary:hover,
.el-button--primary:focus {
    background: #46a6ff;
    border-color: #46a6ff;
    color: #fff;
}

.el-button--primary:active {
    background: #1682e6;
    border-color: #1682e6;
    color: #fff;
    outline: none;
}

.el-button--primary.is-active {
    background: #1682e6;
    border-color: #1682e6;
    color: #fff;
}

.el-button--primary.is-disabled,
.el-button--primary.is-disabled:hover,
.el-button--primary.is-disabled:focus,
.el-button--primary.is-disabled:active {
    color: #fff;
    background-color: #8cc8ff;
    border-color: #8cc8ff;
}

.el-button--primary.is-plain {
    color: #1890ff;
    background: #e8f4ff;
    border-color: #a3d3ff;
}

.el-button--primary.is-plain:hover,
.el-button--primary.is-plain:focus {
    background: #1890ff;
    border-color: #1890ff;
    color: #fff;
}

.el-button--primary.is-plain:active {
    background: #1682e6;
    border-color: #1682e6;
    color: #fff;
    outline: none;
}

.el-button--primary.is-plain.is-disabled,
.el-button--primary.is-plain.is-disabled:hover,
