.el-time-panel {
    margin: 5px 0;
    border: solid 1px #e4e7ed;
    background-color: #fff;
    -webkit-box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
    border-radius: 2px;
    position: absolute;
    width: 180px;
    left: 0;
    z-index: 1000;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.el-time-panel__content {
    font-size: 0;
    position: relative;
    overflow: hidden;
}

.el-time-panel__content::after,
.el-time-panel__content::before {
    content: "";
    top: 50%;
    position: absolute;
    margin-top: -15px;
    height: 32px;
    z-index: -1;
    left: 0;
    right: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding-top: 6px;
    text-align: left;
    border-top: 1px solid #e4e7ed;
    border-bottom: 1px solid #e4e7ed;
}

.el-time-panel__content::after {
    left: 50%;
    margin-left: 12%;
    margin-right: 12%;
}

.el-time-panel__content::before {
    padding-left: 50%;
    margin-right: 12%;
    margin-left: 12%;
}

.el-time-panel__content.has-seconds::after {
    left: calc(100% / 3 * 2);
}

.el-time-panel__content.has-seconds::before {
    padding-left: calc(100% / 3);
}

.el-time-panel__footer {
    border-top: 1px solid #e4e4e4;
    padding: 4px;
    height: 36px;
    line-height: 25px;
    text-align: right;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.el-time-panel__btn {
    border: none;
    line-height: 28px;
    padding: 0 5px;
    margin: 0 5px;
    cursor: pointer;
    background-color: transparent;
    outline: none;
    font-size: 12px;
    color: #303133;
}

.el-time-panel__btn.confirm {
    font-weight: 800;
    color: #1890ff;
}

