    color: #52c41a;
}

.el-alert--info {
    background-color: #f4f4f5;
    color: #909399;
}

.el-alert--info .el-alert__description {
    color: #909399;
}

.el-alert--warning {
    background-color: #fff7e8;
    color: #faad14;
}

.el-alert--warning .el-alert__description {
    color: #faad14;
}

.el-alert--error {
    background-color: #fee9ea;
    color: #f5222d;
}

.el-alert--error .el-alert__description {
    color: #f5222d;
}

