}

.el-message--warning {
    background-color: #FFFBE6;
    border-color: #FFE58F;
}

.el-message--warning .el-message__content {
    color: #606266;
}

.el-message--error {
    background-color: #FFF1F0;
    border-color: #FFA39E;
